  cursor: pointer;
}
.btn p {
  margin: 0;
  padding: 0;
  max-width: max-content;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.02em;
  align-items: center;
  text-transform: uppercase;
  color: #316dc2;
}
.btn span {
  display: flex;
  background-color: #316dc2;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  border: none;
  padding: 0;
  justify-content: center;
  align-items: center;
}
.btn span svg {
  width: 8px;
  height: 8px;
  stroke: #ffffff;
  stroke-width: 2px;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    outline: none;
    transition: 0.3s ease;
    background-color: #316dc2;
  }
  .btn:hover:not(.focus-visible) > p {
    color: #ffffff;
  }
  .btn:hover:not(.focus-visible) > span {
    background-color: #ffffff;
  }
  .btn:hover:not(.focus-visible) > span svg {
    stroke: #316dc2;
    transform: rotate(45deg);
  }
}
.btn.focus-visible:focus {
  outline: none;
  transition: 0.3s ease;
  background-color: #316dc2;
}
.btn.focus-visible:focus > p {
  color: #ffffff;
}
.btn.focus-visible:focus > span {
  background-color: #ffffff;
}
.btn.focus-visible:focus > span svg {
  stroke: #316dc2;
  transform: rotate(45deg);
}
.btn:active {
  transition: 0.3s ease;
}
.btn:active > p {
  opacity: 0.7;
}
.btn[disabled] {
  border-color: #a9afb7;
  pointer-events: none;
  cursor: default;
